import React from "react";
import { useTranslation } from "react-i18next";
import { Page } from "common/components";
import MetaTags from "common/MetaTags";
import WhyMedicalTourismEn from "./why-medicaltourism-en";
import WhyMedicalTourismAr from "./why-medicaltourism-ar";
import RightForMeEn from "./right-for-me-en";
import RightForMeAr from "./right-for-me-ar";
import EssentialsEn from "./essentials-en";
import EssentialsAr from "./essentials-ar";
import { getDescription } from "../descriptions";

const MedicalTourism = ({ contentId, titleId }) => {
  const [t, i18n] = useTranslation();

  const getTitle = () => t(titleId);

  const metaTags = [
    {
      name: "description",
      content: getDescription(contentId, i18n.language),
    },
  ];

  const renderSwitch = () => {
    switch (contentId) {
      case "why-medical-tourism":
        return i18n.language === "ar" ? (
          <WhyMedicalTourismAr />
        ) : (
          <WhyMedicalTourismEn />
        );
      case "is-it-right-for-me":
        return i18n.language === "ar" ? <RightForMeAr /> : <RightForMeEn />;
      case "essentials-to-pack":
        return i18n.language === "ar" ? <EssentialsAr /> : <EssentialsEn />;
      default:
        return null;
    }
  };

  return (
    <Page title={getTitle()}>
      <MetaTags title={getTitle()} tags={metaTags} />
      <div className="content">
        <div className="container">
          <div className="row">
            <div className="col-12">{renderSwitch()}</div>
          </div>
        </div>
      </div>
    </Page>
  );
};

export default MedicalTourism;
