import React from "react";
import { Link } from "react-router-dom";
import { is_it_right_for_me } from "../img";

const RightForMeEn = () => {
  return (
    <div className="terms-content">
      <div className="terms-text">
        <h1>Is Medical Tourism the Right Option for Me?</h1>
        <div className="containter">
          <div className="row">
            <div className="col-md-8 col-sm-12">
              <p>
                In light of the factors explained in "{" "}
                <Link
                  to="/en/medical-tourism/why-medical-tourism"
                  className="text-info"
                >
                  WHY Medical Tourism?
                </Link>{" "}
                " section, you might feel more comfortable to further assess
                your need to benefit from Medical Tourism.
              </p>
              <p>Here is a focused checklist for you:</p>
              <ul>
                <li>
                  Is the treatment more affordable without any sacrifice of
                  quality?
                </li>
                <li>
                  Am I unable to reach certain medical specialists or services -
                  easily?
                </li>
                <li>Are the waiting times for elective procedures shorter?</li>
                <li>
                  Would I like to benefit from more discretion or avoid having
                  to explain to friends and family?
                </li>
                <li>
                  Does the destination country’s medical sector adopt more
                  modern technologies or methods of treatment?
                </li>
                <li>
                  Would I like to travel for recreational, cultural, etc.
                  reasons?
                </li>
              </ul>
            </div>
            <div className="col-md-4 col-sm-12">
              <img
                src={is_it_right_for_me}
                alt="User"
                className="rounded float-left img-fluid"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RightForMeEn;
