import React from "react";
import { Link } from "react-router-dom";
import { is_it_right_for_me } from "../img";

const RightForMeAr = () => {
  return (
    <div className="terms-content">
      <div className="terms-text">
        <h1>هل السياحة العلاجية هي الخيار المناسب لي؟</h1>
        <div className="containter">
          <div className="row">
            <div className="col-md-8 col-sm-12">
              <p>
                في ضوء العوامل الموضحة في "{" "}
                <Link
                  to="/ar/medical-tourism/why-medical-tourism"
                  className="text-info"
                >
                  لماذا السياحة العلاجية؟
                </Link>{" "}
                " قد تشعر براحة أكبر عند قيامكَ بالتحقق من حاجتك للاستفادة من
                السياحة العلاجية.
              </p>
              <p>
                ولذلك قمنا بتحديد النقاط المركزية لك لدراسةِ ما إذا كان ينطبقُ
                عليكَ واحدٌ منها على الأقل:
              </p>
              <ul>
                <li>هل كلفةُ العلاج معقولة دون التضحية بالجودة؟</li>
                <li>
                  هل أنا غيرُ قادرٍ على الوصول إلى أخصائيين أو خدمات في مجالٍ
                  طبّيٍّ معيّن بسهولة في البلدِ التي أعيشُ فيها؟
                </li>
                <li>
                  هل فترات انتظار الإجراءات الطبّية الاختيارية أقصر في بلد
                  السياحةِ العلاجيّة؟
                </li>
                <li>
                  هل أنا راغبٌ في أن أستفيد من مزيد من التكتّم أو أن أتجنّب
                  الاضطرار إلى الشرح للأصدقاء والعائلة؟
                </li>
                <li>
                  هل يعتمد القطاع الطبي في البلد الذي أقصده تقنياتٍ طبيّة أو طرق
                  علاج أكثر تطوّراً؟
                </li>
                <li>هل أرغب في السفر لأسباب ترفيهية وثقافية وما إلى ذلك؟</li>
              </ul>
            </div>
            <div className="col-md-4 col-sm-12">
              <img
                src={is_it_right_for_me}
                alt="User"
                className="rounded float-left img-fluid"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RightForMeAr;
