import React from "react";
import { why_medical_tourism } from "../img";

const WhyMedicalTourismEn = () => {
  return (
    <div className="terms-content">
      <div className="terms-text">
        <h1>WHY Medical Tourism?</h1>
        <div className="containter">
          <div className="row">
            <div className="col-md-6 col-sm-12">
              <p>
                Medical Tourism is a term denoting travel across international
                borders for the purpose of receiving medical care.{" "}
              </p>
              <p>
                Medical tourism, as such, has existed since ancient times and
                with each passing year more and more people are interested in
                overseas medical and health services.{" "}
              </p>
              <p>
                People can travel from developed to developing countries or vice
                versa for a number of motives that include:
              </p>
              <ul>
                <li>
                  Economics: high cost of care, inadequacy or the complete
                  absence of health insurance, or combination of medical tourism
                  with traditional tourism;
                </li>
              </ul>
              <ul>
                <li>
                  Quality: more modern level of medical technologies in the
                  destination country or more qualified medical care and
                  services, or limitation of variety of medical institutions and
                  methods and treatments in the home country;
                </li>
              </ul>
            </div>
            <div className="col-md-6 col-sm-12">
              <img
                src={why_medical_tourism}
                alt="User"
                className="rounded float-left img-fluid"
              />
            </div>
          </div>
        </div>
        <br />
        <p>
          <ul>
            <li>
              Procedural and Social: procedures are not provided in home
              country, restrictions on access to certain specialists,
              challenging treatment protocols, or long queues for certain types
              of medical services (especially in cases of surgery),{" "}
            </li>
          </ul>
          <ul>
            <li>
              Personal: the need to maintain confidentiality and anonymity in:
              treatment, previous personal experience or the experience of other
              people, or the desire to travel.
            </li>
          </ul>
        </p>

        <p>
          Furthermore, a large number of people desire to be constantly aware of
          their health, and to pay great attention to adopting a healthy
          lifestyle and physical fitness. These people seek therapeutic,
          rehabilitative and preventive examinations and procedures provided by
          health resorts, prevention and treatment facilities.
        </p>
        <p>
          Additionally, patients with chronic cardiovascular, orthopedic,
          rheumatologic and neurological diseases seek recovery services abroad.
        </p>
        <p>
          Also, people living abroad may return to their home country for the
          treatment they require.
        </p>
        <p>
          Combined with the ease of travel between countries, these factors have
          led to the growth of demand on medical tourism around the world.
        </p>
      </div>
      <div className="terms-text">
        <h2>FEATURED Types of Medical Tourism</h2>
        <h4>SENIOR TOURISM AND ACCESSIBLE TOURISM</h4>
        <ul>
          <li>Senior Tourism (Sightseeing Tours, Occupational Therapies) </li>
          <li>
            Elder Care Services (Nursing Home and Rehabilitation Services){" "}
          </li>
          <li>Rehabilitation Services in Clinical Hotels </li>
          <li>Special Care and Sightseeing Tours for the Disabled </li>
        </ul>
        <p>
          Service Locations: Clinical Hotels, Recreation Areas, Holiday Villages
          and Nursing Homes etc.{" "}
        </p>
      </div>
      <div className="terms-text">
        <h4>THERMAL HEALTH TOURISM AND SPA-WELLNESS</h4>
        <ul>
          <li>Spa treatments and Physiotherapies</li>
          <li>Thalassotherapy</li>
          <li>Hydrotherapy and exercises </li>
          <li>Balneotherapy and Peloidotherapy </li>
          <li>
            Climatotherapy
            <br />
            <br />
            Service Locations: Thermal Spring and Spa Centers{" "}
          </li>
        </ul>
      </div>
      <div className="terms-text">
        <h5>Featured MEDICAL TOURISM Specialties:</h5>
        <ul>
          <li>
            Advanced Treatments (Cardiovascular Surgery, Radiotherapy,
            CyberKnife etc.)
          </li>
          <li>Transplantation</li>
          <li>Infertility (IVF applications)</li>
          <li>Eye Treatment, Dental Treatment, Dialysis etc.</li>
        </ul>
      </div>
    </div>
  );
};

export default WhyMedicalTourismEn;
