import React from "react";
import { essentials_to_pack_01 } from "../img";
import { essentials_to_pack_02 } from "../img";

const EssentialsEn = () => {
  return (
    <div className="terms-content">
      <div className="terms-text">
        <h1>Essentials to Pack for Your Medical Tourism Trip</h1>
        <div className="containter">
          <div className="row">
            <div className="col-12">
              <p>
                We want to be sure that your medical trip go as smooth and
                pleasant as possible. Hence we will help you make a list of
                things you might possibly need while packing so that you check
                it before leaving to ensure your trip gets off to a good start
                avoiding stress caused by forgetting something important.
              </p>
            </div>
            <div className="col-12">
              <img
                src={essentials_to_pack_02}
                alt="User"
                className="img-fluid rounded mx-auto d-block"
                style={{ width: "65%" }}
              />
            </div>
          </div>
        </div>
      </div>
      <div className="terms-text">
        <h4>Documents and Paperwork</h4>
        <p>
          Getting your papers in order with plenty of time to spare is always a
          good idea. If you wait until the last minute, you are setting yourself
          up for a possible last-minute glitch. For instance, ordering a
          passport to replace an expired one may take some time.
        </p>
        <ul>
          <li>Medical Papers:</li>
          <p>
            Medical papers and records are a necessity and should be at the top
            of your list. Just to be safe, it is always a good idea to have an
            extra copy of key documents in different suitcases in case a luggage
            is lost.
          </p>
          <li>Passports and Visas:</li>
          <p>
            Keep your passport close at all times and have some foreign currency
            on hand for incidentals.
          </p>
        </ul>
        <h5>
          Beforehand, check the rules for passports in different countries in
          addition to whether or not the destination country has visa
          requirements.
        </h5>
      </div>
      <div className="terms-text">
        <h4>Currency and Finances</h4>
        <p>
          Most of the time, exchanging currency is easier done ahead of time at
          a trusted entity in your home country.
        </p>
        <p>
          In T&uuml;rkiye, it is recommended that you bring US dollars or
          European Euros from your home country and exchange them to Turkish
          Lira upon arrival.
        </p>
        <p>
          It's always a good idea to get bills in smaller denominations so
          there is no problem using them and getting change for low cost items.
        </p>
      </div>
      <div className="terms-text">
        <h4>Medication and Pain Relievers</h4>
        <div className="containter">
          <div className="row">
            <div className="col-md-8 col-12">
              <p>
                Given the difference of medication available in each country,
                some medicine may be harder to find in the country where you are
                headed.
              </p>
              <p>
                So it's always a good idea to get the basic medications
                you might need to use for a chronic condition you have,
                painkillers and over-the-counter medications.
              </p>
              <p>
                As you'll be eating different foods it is a good idea to
                have medicine to treat an upset stomach or motion sickness.
                &zwj; Please remember that tablets travel better than liquids
                and gels, especially if you're flying. &zwj;
              </p>
            </div>
            <div className="col-md-4 col-12">
              <img
                src={essentials_to_pack_01}
                alt="User"
                className="rounded img-fluid"
                style={{ width: "100%", marginBottom: "1.5rem" }}
              />
            </div>
          </div>
        </div>
      </div>
      <div className="terms-text">
        <h4>Comfortable Clothing</h4>
        <p>
          You will need to pack clothes taking into consideration to check
          weather forecast for the period of your trip.
        </p>
        <p>
          Comfortable clothing that works for staying in a hospital should be
          packed.
        </p>
      </div>
      <div className="terms-text">
        <h4>Insect Repellant</h4>
        <p>
          You might think about including these to ensure as much comfort as you
          can. Furthermore, some areas popular as medical tourism destinations
          have a lot of insects.
        </p>
        <h4>Comforts from Home</h4>
        <p>
          Bring along with you items you consider essential comforts, such as
          pillows, coffee, or even bed linens. &zwj;
        </p>
      </div>
      <div className="terms-text">
        <h4>Emergency Contacts</h4>
        <p>
          It is important to have with you in an easily accessible notebook the
          contact details of doctors, hospital, hotel and any other important
          destination. If you are in a country where you don't speak the
          language just show the address and get some help. &zwj;
        </p>
      </div>
      <div className="terms-text">
        <h4>Travel Companion</h4>
        <p>
          Traveling in pairs is something to consider especially if you desire
          to feel safer during trip, treatment, or to receive additional
          supervision and assistance in unforeseen situations.
        </p>
      </div>
    </div>
  );
};

export default EssentialsEn;
