import React from "react";
import { essentials_to_pack_01 } from "../img";
import { essentials_to_pack_02 } from "../img";

const EssentialsAr = () => {
  return (
    <div className="terms-content">
      <div className="terms-text">
        <h1>
          القائمة الأساسية عند حزم أمتعتك استعداداً لرحلة السياحة العلاجية&nbsp;
        </h1>
        <div className="containter">
          <div className="row">
            <div className="col-12">
              <p>
                نحرص على أن تكون رحلتك العلاجيّة سلسة وممتعة قدر الإمكان. ولذلك
                سوف نساعدك في إعداد قائمة بالأشياء التي تحتاج أن تتحقق من
                تجهيزها أثناء حزم أمتعتك قبل مغادرتك، للتأكد من أن تبدأ بدايةً
                موفّقةً مع تجنّب أي ضغطٍ نفسيّ ناجم عن نسيان شيء مهم.
              </p>
            </div>
            <div className="col-12">
              <img
                src={essentials_to_pack_02}
                alt="User"
                className="img-fluid rounded mx-auto d-block"
                style={{ width: "65%" }}
              />
            </div>
          </div>
        </div>
      </div>
      <div className="terms-text">
        <h4>المستندات والأوراق</h4>
        <p>
          دائمًا ما يكون تحضير أوراقك قبل وقتٍ كافٍ من سفرك فكرة جيدة، حتى لا
          يواجهك خللٌ في اللحظة الأخيرة قد يصعبُ إصلاحه. فعلى سبيل المثال قد
          يستغرق تجديد جواز سفر منتهي الصلاحية بعض الوقت الذي قد لا يتوفّر لديك
          قبل سفرك.
        </p>
        <ul>
          <li>الأوراق الطبية:</li>
          <p>
            الأوراق والسجلات الطبية ضرورية ومن أهمّ ما يجب وضعه على رأس قائمتك.
            كما ومن الأفضل دائمًا أن يكون لديك نسخة إضافية من المستندات الرئيسية
            في حقائب مختلفة في حالة فقدان الأمتعة.
          </p>
          <li>الجوازات والتأشيرات:</li>
          <p>
            احتفظ بجواز سفرك قريبًا في جميع الأوقات واحتفظ ببعض العملات الأجنبية
            في متناول اليد لتغطية الأمور الطارئة.
          </p>
        </ul>
        <h5>
          قبل ذلك، تحقق من قواعد جوازات السفر في مختلف البلدان بالإضافة إلى ما
          إذا كان بلد المقصد لديه متطلبات تأشيرة أم لا.
        </h5>
      </div>
      <div className="terms-text">
        <h4>تحويل العملة</h4>
        <p>
          في معظم الأحيان يكون من الأسهل والأكثر أماناً تحويل العملات في وقت
          مبكر بينما في بلدك الأمّ من مصدرٍ تعرفه.
        </p>
        <p>
          إذا أردتَ أن تسافرَ إلى تركيا مثلاً يوصى بإحضار الدولار الأمريكي أو
          اليورو الأوروبي من بلدك الأصلي واستبداله بالليرة التركية عند وصولك.
        </p>
        <p>
          كما وينصح أيضاً بأن تحوّلَ العملات بفئاتٍ صغيرة حتى يتيسّر لك
          استخدامها؛ فمثلاً عند شرائك احتياجاتك قليلة التكلفة يسهل عليكَ الدفع
          بها والحصول على الباقي.
        </p>
      </div>
      <div className="terms-text">
        <h4>&nbsp;الأدوية ومسكنات الآلام</h4>
        <div className="containter">
          <div className="row">
            <div className="col-md-8 col-12">
              <p>
                نظرًا لاختلاف الأدوية المستخدمةِ والمتاحة في كلّ بلد قد يكون من
                الصعب عند سفرك العثور على بعض الأدوية التي تحتاجها واعتدتَ
                عليها.
              </p>
              <p>
                لذلك ينصحُ دائمًا بإحضار الأدوية الأساسية التي قد تحتاج
                استخدامها لحالة مرضية مزمنة لديك ، إضافةً لمسكنات الألم والأدوية
                التي لا تستلزم وصفة طبية كدواء علاج اضطراب المعدة والأمعاء أو
                دوار الحركة، خصوصاً في ضوء تغيّر الأطعمة التي تتناولها ونظام
                الأكل لديك على الأغلب.&nbsp;
              </p>
              <p>
                يرجى تذكّر أن الأدوية التي تمّ صنعها على هيئة حبوب يسهل حملها
                واستخدامها أثناء السفر أكثر من السائلة، خاصة إذا قمت بالسفر
                بواسطة الطائرة.&nbsp;
              </p>
            </div>
            <div className="col-md-4 col-12">
              <img
                src={essentials_to_pack_01}
                alt="User"
                className="rounded img-fluid"
                style={{ width: "100%", marginBottom: "1.5rem" }}
              />
            </div>
          </div>
        </div>
      </div>
      <div className="terms-text">
        <h4>&nbsp;ملابس مريحة</h4>
        <p>
          سوف تحتاج عند تحضير الملابس أن تأخذ بعين الاعتبار توقّعات الطقس
          المتعلّقة بفترة رحلتك.
        </p>
        <p>
          كما يجب أن تتضمّن ملابسك قطعاً مريحةً تناسب الحاجة للبقاء في المستشفى
          عند الحاجة.
        </p>
      </div>
      <div className="terms-text">
        <h4>&nbsp;طارد الحشرات</h4>
        <p>
          قد تفكر في أخذها معك لضمان قدرٍ أكبر من الراحة، خصوصاً إن كنت تنوي
          السفر إلى بعض المناطق المعروفة بوجود الحشرات.
        </p>
      </div>
      <div className="terms-text">
        <h4>بواعث الراحة والسكينة</h4>
        <p>
          قد يكون من الجميل أن تحضر معك بعض الأشياء التي تعتبرها مصدراً لراحتك
          وسكينتك، والتي قد تكون وسادةً مثلاً أو قهوةً معيّنة تجلبُ لك السعادة.
        </p>
      </div>
      <div className="terms-text">
        <h4>&nbsp;اتصالات الطوارئ</h4>
        <p>
          من المهم أن تحملَ معك في دفترٍ صغيرٍ يسهل الوصول إليه تفاصيل الاتصال
          بالأطباء والمستشفى والفندق وأي وجهة أخرى مهمة أثناء سفرك. وتبرز أهمّية
          ذلك بشكلٍ كبير إذا كنت في بلد لا تتحدث لغته حيث يكفيكَ أن تظهر العنوان
          إذا أردت الحصول على بعض المساعدة.
        </p>
      </div>
      <div className="terms-text">
        <h4>&nbsp;رفيق السفر</h4>
        <p>
          قد ترغب في اختيار أن تسافر مع رفيقٍ خاصة إذا كان ذلك يزيدُ شعورك
          بالأمان أثناء الرحلة أو العلاج، أو عند الحاجةِ لوجود إشرافٍ إضافيّ
          أومساعدةٍ في المواقف غير المتوقعة.
        </p>
      </div>
    </div>
  );
};

export default EssentialsAr;
